// global rules
@font-face {
  font-family:"Montserrat";
  src: url("fonts/Montserrat-Regular.ttf") format("truetype")
}
@font-face {
  font-family:"TiemposHeadline";
  src: url("fonts/TiemposHeadline-Regular.otf") format("opentype")
}

body {
  font-family: $mainFont;
  background-color: $subColor;
  color:$txt;
  padding-top:$navHeight;
}
h1, .h1, h2, h3 {
  color:#1D3251;
}
h2 {font-weight:900}
a, a:hover{
  text-decoration: none;
  color:inherit
}
// global classes
.wrapper {
  max-width:1550px;
  margin:auto;
}

.heading-flip {
  margin:0 auto;
  transform:rotate(-90deg);
  display:inline-block;
}

.heading {
  position: relative;
  text-align: center;
  margin-top:-15px;
  &:after {
    content:'';
    position:absolute;
    bottom:0px;
    left:0;
    width:100%;
    height:10px;
    background:url('../imgs/menu-border.png') center no-repeat;
    background-size:contain;
  }
}

// fonts
@font-face {
  font-family: 'Geovana';
  src: "fonts/Geovana.otf" format('opentype');
}