.home {
  // header
  .header {
    height:calc(100vh - #{$navHeight});
    // max-height:700px;
    padding-bottom:$margin;
    background:url('../imgs/header.jpg') right no-repeat;
    background-size:contain;
    border-bottom:1px solid;
    border-color:#{$grey};

    .row {
      height:calc(100vh - #{$navHeight});
      max-height:700px;
      align-items: center;
    }

    .txt {margin:0 30px}

    h1 {
      font-size:30px;
      @include responsiveFont(25,50,320,1200);
      font-family: $specialFont;
    }

    p {
      font-size: 24px;
      color:#000;
      margin-top:60px;

    }
    a {
      background-color:$mainColor;
      color: #fff;
      height:50px;
      border-radius:25px;
      line-height: 50px;
      display:inline-block;
      font-size:14px;
      margin-top:30px;
      max-width:320px;
    }
  }

  // serveral boxes 
  .how,.stats,.referal {
    border-bottom:1px solid;
    border-color:#{$grey};
    padding-bottom:50px;

    h2 {
      color:#1D3251;
      padding:30px 0;
    }

    .box {
      background-color:#fff;
      padding:#{$padding} 0;
      transition:all .3s linear;
      border:1px solid #f1f1f1;
      max-width:300px;
      margin:0 auto;
      cursor: pointer;
      box-shadow: 0 0px 50px #{darken(#fff, 5%)};
      &:hover {
        box-shadow: 0 10px 50px #{darken(#fff, 15%)};
      }
      .img {
        width:100px;
        height:100px;
        margin:0 auto 10px auto;
        i {
          width:100px;
          font-size:100px;
          color:$mainColor
        }
      }
      span {
        font-size:45px;
        color:$mainColor;
      }
      h3 {
        color:#1D3251;
        font-weight:bold;
        max-width:200px;
        margin:0px auto;
        text-align:center;
        font-size:16px;
      }
    }
  }

  .referal {
    .box .img {
      margin:10px auto 0 auto;
    }
    .box {
      position:relative;
      &.after:after {
        content:'';
        position: absolute;
        width:120px;
        height:65px;
        background:url('../imgs/arrow1.png') center no-repeat;
        background-size:contain;
        right:-60px;
        top:50%;
        transform:translateY(-50%);
        z-index:2;
      }
      @media (max-width:767px) {
        &.after:after {
          right: 50%;
          top: auto;
          bottom: -18px;
          transform: translateX(50%) rotate(90deg);
          width: 50px;
          height: 30px;
        }
      }
    }
  }

  // about
  .about, .benefits,.referalInfo {
    border-bottom:1px solid #{$grey};
    h2 {
      padding-top:30px;
      font-size:40px;
    }
    .txt {
      padding:$padding 0;
      font-size:20px;
      color:#1D3251;
      h2 {
        margin-bottom:40px;
        padding-top:0;
        font-size:35px;
      }
      ul li {
        margin-bottom:5px;
      }
      ul li.first {
        color:#337ab7;
        i, span {
          color:#25d366;
          margin:0 5px;
        }
      }
    }
    .img {
      padding-top:60px;
      margin:auto
    }
    
    a {
      background-color:$mainColor;
      color: #fff;
      height:50px;
      border-radius:25px;
      line-height: 50px;
      display:inline-block;
      font-size:14px;
      max-width:320px;
      margin:0 auto 40px auto;
    }
  }

  //contact
  .contact {
    .img {
      max-width:400px;
      margin:auto;
      position:relative;
      top:50%;
      transform: translateY(-50%);
      &:before, &:after {
        content:'';
        position:absolute;
        top:130px;
        left:20px;
        background-color:#bd6300;
        width:50px;
        height:80px;
        border-radius: 50%;
        filter:blur(40px);
        z-index: -1;
        animation: smoke 3s ease infinite;
        transform: scale(0);
      }
      &:after {
        animation-delay:1s;
        animation-duration:2s;
        left:10px;
      }
    }
    form {
      max-width:500px;
      margin:0 auto;
      color:$mainColor;
      padding:#{$padding} 0;
      .h1 {
        @include responsiveFont (30,60,320,1550);
        padding-bottom:10px;
        margin-bottom:$margin;
      }
      input,textarea {
        width:100%;
        background-color:$sub2Color;
        border:none;
        border-radius:10px;
        padding:10px;
        outline: none;
      }
      textarea {
        margin-top:$margin / 2;
        height:200px;
        border-radius:10px 10px 0 0px;
      }
      button {
        background-color:$yellow;
        width:100%;
        margin:auto;
        padding:10px 0;
        border-radius:0px 0px 10px 10px;
        border:none;
        margin-top:-3px;
        cursor:pointer;
        color:$txt;
        font-size:20px;
        transition:all .5s ease;
        border:1px solid #{$yellow};
        &:hover {
          background-color:$subColor;
          border-color:$sub2Color
        }
      }
    }
  }

  // location
  .location {
    border-top:1px solid #{$grey};
    padding:#{$padding} 0;
    .img {
      max-width:500px;
      margin:auto;
    }
    .info {
      max-width:400px;
      margin:auto;
      position:relative;
      top:50%;
      transform: translateY(-50%);
      .h1 {
        @include responsiveFont (30,50,320,1550);
        padding-bottom: 10px;
      }
      .box {
        border-radius: 15px;
        padding:#{$padding} 15px;
        background-color:$yellow;
        margin:#{$margin} auto 0 auto;
        font-size:14px;
        li {
          max-width:320px;
          margin:auto;
          padding:15px 0;
          border-bottom:1px dashed #{$mainColor};
          position:relative;
          &:first-of-type {
            padding-top:0;
          }
          &:before {
            content:'';
            position:absolute;
            bottom:-8px;
            left:-8px;
            background:url('../imgs/icon-bg1.png') center no-repeat;
            background-size:contain;
            width:30px;
            height:30px;
          }
          i {
            color:$mainColor;
            margin-right:$margin / 2;
            text-align:center;
            font-size:12px;
          }
        }
      }
    }
  }
  
}

@keyframes smoke {
  0%   {transform: translateY(0px) translateX(0) scale(0);opacity:1}
  100% {transform: translateY(-150px) translateX(-100px) scale(1);opacity:0}
}

// responsive
@media (max-width:991px) {
  .home {
    // header
    .header {
      padding-top:$padding;
      background:url('../imgs/header.jpg') bottom no-repeat;
      background-size:contain;
      max-height:650px;
      .row {
        max-height: 100%;
        height:auto;
        h1 {
          transform: none;
        } 
        p {
          font-size: 16px;
          margin-top:15px;
        }
        img {
          max-width:250px;
          margin-bottom:$margin / 2;
        }
      }
    }

    // about
    .about {
      .txt {
        padding:15px 0;
      }
      .img {
        max-width:350px;
        margin:auto;
      }
    }

    .about .img, .benefits .img, .referalInfo .img {
      padding-top:0px;
    }
  }
}

@media (max-width:991px) {

  @media (min-width:768px) {

    // feats
    .feats .row {
      position:relative;
      &:before,&:after {
        content: '';
        position: absolute;
      }
      &:before {
        left: 50%;
        top: 0;
        border-right: 1px dashed#{$mainColor};
        width: 1px;
        height: 100%;
      }
      &:after {
        top: 50%;
        left: 0%;
        border-bottom: 1px dashed#{$mainColor};
        height: 1px;
        width: 100%;
      }
    }
  }

  @media (max-width:767px) {
    .feats .row .col-lg-3:not(:last-of-type) .box {
      border-bottom: 1px dashed#{$mainColor};
    }
  }
}