.nav {
  background-color:$subColor;
  height:$navHeight;
  line-height: $navHeight;
  color:$mainColor;
  border-bottom:1px solid #{$grey};
  position:relative;
  transition:all .5s ease;
  position:fixed;
  top:0;
  left:0;
  width:100%;
  z-index:9999999;
  font-size:14px;
  &.fixed {
    height:$navHeightFixed;
    line-height:$navHeightFixed;
    &:before {
      content:'';
      position: absolute;
      top:0;
      left:0;
      width:100%;
      height:100%;
      box-shadow:0 0 50px #{lighten($mainColor, 70%)};
      z-index:-1
    }
    ul.toggled {
      top:$navHeightFixed;
    }
    img {
      width:$navHeightFixed - 10px;
    }
  }

  ul {
    display:flex;
    justify-content: space-between;
    margin:0;
  }
  img {
    width:$navHeight - 20px;
    transition:all .5s ease;
  }
  .toggle-btn {
    position: relative;
    top:50%;
    height:30px;
    cursor: pointer;
    span {
      height:1px;
      width:30px;
      background-color:#{$mainColor};
      display:block;
      transition:all .5s ease;
      &:nth-of-type(2) {
        margin:5px 0;
      }
    }
    &.active {
      span:nth-of-type(1) {
        transform: rotate(45deg);
      }
      span:nth-of-type(2) {
        display:none;
      }
      span:nth-of-type(3) {
        transform: rotate(-45deg);
      }
    }
  }
}

// responisive
@media (max-width:991px) {
  .nav {
    .logo:not(li) {
      display:block !important
    }
    ul {
      visibility: hidden;
      opacity:0;
      display:block;
      position:absolute;
      width:100%;
      left:0;
      top:$navHeight;
      background-color:$mainColor;
      color:$subColor;
      &.toggled {
        z-index:9999;
        opacity:1;
        visibility:visible;
        transition:all .5s ease;
        box-shadow:0 0 50px #{lighten($mainColor, 70%)};
  
        li {
          line-height:30px;
          padding:15px;
          transition:all .5s ease;
          &:hover {
            text-indent: 10px;
          }
          &:not(:last-of-type) {
            border-bottom:1px dashed #{$mainColor};
          }
          &.logo {
            display:none;
          }
          a {
            display:block;
          }
        }
      }
    }
    .toggle-btn {
      display:block !important;
    }
  }
}